.homepage-7 {
  display: block;
  position: relative;
  overflow: hidden;

  .wrapper-homepage {
    border-radius: 8px;

    .wrapper-flex {
      width: 100%;
      height: 690px;

      @media screen and (max-width: 767.98px) {
        height: auto;
      }

      flex-direction: row;
      gap: 2rem;

      position: relative;
      border: 1px solid #cde2e7;
      border-radius: 8px;

      margin: 2.5rem 0 3rem 0;
      overflow: hidden;

      .flex-info {
        display: block;
        width: 55%;
        height: 100%;

        @media screen and (max-width: 1433.98px) {
          width: 45%;
        }

        @media screen and (max-width: 1279.98px) {
          width: 100%;
        }

        .info-box {
          display: flex;
          flex-direction: column;
          align-items: start;
          justify-content: center;

          height: 100%;
          padding: 1rem 1rem 1rem 3rem;

          @media screen and (max-width: 767.98px) {
            padding: 0.75rem 1.5rem;
          }
          @media screen and (max-width: 575.98px) {
            padding: 3rem 1.75rem;
          }

          .subtitle {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: flex-start;
            gap: 10px;

            .line {
              display: block;
              width: 48px;
              height: 1.5px;
            }
          }

          .title {
            font-size: 62px;
            font-weight: 800;
            line-height: 72px;

            @media screen and (max-width: 767.98px) {
              font-size: 42px;
              line-height: 52px;
            }
          }

          .input-group {
            display: flex;
            justify-content: flex-start;
            align-items: center;

            width: 80%;
            background: #fff;
            border-radius: 40px;
            margin-bottom: 48px;
            padding: 0.5rem 9rem 0.5rem 1.5rem;

            @media screen and (max-width: 575.98px) {
              margin-bottom: calc(48px / 2);
            }

            @media screen and (max-width: 767.98px) {
              width: 100%;
            }

            .form-custom {
              width: 100%;
              height: 3rem;
              outline: none;
            }

            button {
              top: 50%;
              right: 12px;
              transform: translateY(-50%);
            }
          }

          .supporter {
            display: flex;
            justify-content: flex-start;
            align-items: center;
            gap: 0.75rem;

            @media screen and (max-width: 575.98px) {
              flex-flow: row wrap;
            }

            .view {
              width: 153px;
            }
          }
        }
      }

      .flex-image {
        display: flex;
        align-items: stretch;
        justify-content: space-around;
        flex-direction: row;

        width: calc(100% - 55%);
        height: auto;
        gap: 2rem;

        @media screen and (max-width: 1433.98px) {
          width: 55%;
        }
        @media screen and (max-width: 1279.98px) {
          display: none;
        }

        .gallery-box {
          display: flex;
          flex-direction: column;
          row-gap: 2rem;

          position: relative;
          width: 50%;
          height: auto;
          transition: top 150ms linear;

          .view {
            img {
              width: 100%;
              height: auto;
              border-radius: 8px;
            }
          }
        }
      }
    }
  }
}

.swiper-button-prev,
.swiper-button-next {
  display: none;
}
