.team {
  .team-banner {
    .grid-wrapper {
      padding: 5.75rem 0;

      @media screen and (max-width: 991.98px) {
        flex-wrap: wrap;
      }

      .grid-info {
        width: 45%;
        padding-right: 8.75rem;

        @media screen and (max-width: 1279.98px) {
          width: 50%;
          padding-right: 3rem;

          .customer-said {
            margin-top: 3rem;
          }
        }

        @media screen and (max-width: 991.98px) {
          width: 100%;
        }

        .title {
          font-size: 48px;
          font-weight: 800;
          line-height: 56px;

          @media screen and (max-width: 1279.98px) {
            font-size: 32px;
            line-height: 48px;
          }

          @media screen and (max-width: 991.98px) {
            font-size: 42px;
            line-height: 62px;
          }
          @media screen and (max-width: 575.98px) {
            font-size: 32px;
            line-height: 48px;
          }
        }

        .button-wrapper {
          margin-top: 2.5rem;

          .btn-contact {
            font-size: 14px;
            color: #ffe7bb;
            padding: 16px 24px;

            @media screen and (max-width: 575.98px) {
              padding: 16px 18px;
            }
          }
        }

        .btn-support {
          font-size: 14px;
          font-weight: 700;
          line-height: 18px;
          color: #071920;

          padding: 16px 30px;

          @media screen and (max-width: 575.98px) {
            padding: 16px 16px;
          }
        }
      }

      .grid-gallery {
        width: 55%;

        @media screen and (max-width: 1279.98px) {
          width: 50%;
        }
        @media screen and (max-width: 991.98px) {
          width: 100%;
        }

        .grid-image {
          display: block;
          position: relative;
          margin: auto auto 0 auto;
          z-index: 1;
        }

        .arrows_down {
          width: 30px;
          height: auto;
          right: 25px;
          bottom: 100px;

          @media screen and (max-width: 1279.98px) {
            width: 20px;
          }

          @media screen and (max-width: 419.98px) {
            width: calc(20px / 1.25);
            bottom: 95px;
          }
          @media screen and (max-width: 359.98px) {
            bottom: 85px;
          }
        }

        .arrows_right {
          width: 123px;
          height: auto;

          left: 0;
          bottom: 60px;

          @media screen and (max-width: 1279.98px) {
            width: 84px;
            bottom: 40px;
          }
          @media screen and (max-width: 419.98px) {
            width: calc(84px / 1.25);
            bottom: 40px;
          }
          @media screen and (max-width: 359.98px) {
            bottom: 20px;
          }
        }

        .layout-border {
          display: block;
          position: relative;

          &::before {
            content: '';
            display: block;
            position: absolute;

            width: 100%;
            height: 100%;
            border: 4px solid #aee6b9;
            z-index: 0;

            @media screen and (max-width: 1279.98px) {
              height: 98%;
            }
          }

          &.first {
            &::before {
              top: 16px;
              left: -13px;
            }
          }

          &.second {
            &::before {
              top: -13px;
              left: 13px;

              @media screen and (max-width: 575.98px) {
                left: 8px;
              }
            }
          }
        }
      }
    }
  }

  .team-board-member {
    .grid-wrapper {
      display: grid;
      grid-template-columns: repeat(4, minmax(0, 1fr));
      column-gap: 40px;

      @media screen and (max-width: 1279.98px) {
        grid-template-columns: repeat(3, minmax(0, 1fr));
        column-gap: 30px;
      }
      @media screen and (max-width: 1023.98px) {
        grid-template-columns: repeat(2, minmax(0, 1fr));
        column-gap: 30px;
      }
      @media screen and (max-width: 575.98px) {
        grid-template-columns: repeat(1, minmax(0, 1fr));
        column-gap: 30px;
      }

      .box-element {
        display: block;
        position: relative;

        padding: 1.75rem 0;
        border-bottom: 1px solid #cde2e7;

        .flex-in-row {
          display: flex;
          flex-direction: row;
          align-items: flex-start;
          justify-content: flex-start;
          gap: 1rem;

          .view {
            display: block;
            width: 35%;

            .img-gallery {
              object-fit: cover;
              border-radius: 8px;
              image-rendering: -webkit-optimize-contrast;
            }
          }

          .information {
            display: block;
            width: 65%;

            h3.title {
              font-size: 18px;
              font-weight: 700;
              line-height: 23px;
            }

            .department {
              font-size: 12px;
              font-weight: 700;
              line-height: 18px;
            }

            ul.flex {
              margin-top: 1.5rem;
              gap: 0.5rem;

              @media screen and (max-width: 575.98px) {
                margin-top: 2.5rem;
              }
            }
          }
        }

        .info-cont {
          p {
            font-size: 14px;
            font-weight: 500;
            line-height: 18px;

            @media screen and (max-width: 575.98px) {
              font-size: 16px;
              line-height: 28px;
            }
          }
        }
      }
    }
  }

  .team-have-question {
    padding: 8rem 0;

    @media screen and (max-width: 575.98px) {
      padding: 2.5rem 0;
    }

    .grid {
      padding: 0 0.75rem;

      .title {
        font-size: 48px;
        font-weight: 800;
        line-height: 56px;

        @media screen and (max-width: 1279.98px) {
          font-size: 32px;
          line-height: 48px;
        }
      }
    }
  }
}
