.slide-small-guys {
  position: relative;
}

.slide-big-guys .owl-carousel,
.slide-small-guys .owl-carousel {
  position: relative;
}

.slide-big-guys .owl-dots {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  z-index: 2;
}

.slide-big-guys .btn-prev,
.slide-big-guys .btn-next,
.slide-small-guys .btn-prev,
.slide-small-guys .btn-next {
  display: block;
  width: 50px;
  height: 50px;
  background: #161616;
  color: #fff;
  border: 0;
  border-radius: 50%;
  outline: none;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 2;
}

.slide-big-guys .btn-prev[disabled=disabled],
.slide-big-guys .btn-next[disabled=disabled],
.slide-small-guys .btn-prev[disabled=disabled],
.slide-small-guys .btn-next[disabled=disabled] {
  opacity: 0.25;
