.slide-big-guys .btn-prev,
.slide-small-guys .btn-prev {
  left: -60px;
}

.slide-big-guys .btn-next,
.slide-small-guys .btn-next {
  right: -60px;
}

.slide-small-guys .owl-item {
  width: 100%;
  height: auto;
  opacity: 0.5;
  transition: opacity 550ms ease;
}

.slide-small-guys .owl-item img {
  width: 100%;
